body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.btn{
    background-color: #095B83;
    border: 0;
    padding: 1em 2.4em ;
    color: white;
    border-radius: 8px;
    transition: all 0.17s ease;
}

.btn:hover,
.btn:focus,
.btn:active{
    background-color: #236687;
}

@media screen and (max-width:390px) {
    .btn{
        width: 47%;

        font-size: 0.8em;
        padding: 1em 0em;
    }
}
.switch{
    width: 4em;
    height: 2em;
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: relative;
    border-radius: 3px;
}
.switch .icon{
    z-index: 1;
}
.switch .thumb{
    position: absolute;
    width: 50%;
    height: 100%;
    left: 0;
    z-index: 0;
    transition: left 0.17s ease;
    border-radius: 3px;
}
.header {
    width: 100%;
    align-items: center;
    display: flex;
    justify-content: space-between;
    height: 4em;


}

.btn-desktop {
    display: block;
}

.btn-mobile {
    display: none;
}

.header>* {
    margin: 1em;
}

@media screen and (max-width:390px) {
    .btn-desktop {
        display: none;
    }

    .btn-mobile {
        display: block  ;
    }

}
.model{
    width: 30em;
    height: 15em;
    display: flex;
    align-items: center;
    justify-content: center;

    position: absolute;
    top: 30vh;
    left: 40vw;
    z-index: 4;

}
.model form{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 1em;
    gap: 1em;

}
.model form input{
    background-color: transparent;
    width: 80%;
    height: 2em;
    border: 0;
    border-radius: 5px;
}
.black-drop{
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.5;
    margin: 0;
    z-index: 3;
    left: 0;
    top: 0;
    position: absolute;
}

@media screen and (max-width:390px) {
    .model{
        width: 90vw;
        left: 0;
    }
}
.empty-cricle {
    padding: 0;
    border-radius: 50%;
    border: 1px solid black;
}
.task-card {
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: 0 2px 8px 0 #66666640;
    height: 70px;
    border-radius: 8px;
}

.task-card > *{
    margin: 1em !important;
}
.task-card .task-body {
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items:flex-start;
    justify-content: center;
    grid-gap: 0;
    gap: 0;
}

.task-card .task-body * {
    margin: 0; 
}

.task-card .task-body input{
    width: 100%;
    padding: 0.2em 0;
    border-radius: 3px;
    outline: none;
    font-size: 1em;
    background-color: transparent;
}


.task-card .task-body input:disabled{
    border: none !important;

}
.task-card .task-body p{
    color: gray;
}
.task-card .control{
    display: flex;
    grid-gap: 1em;
    gap: 1em;
}
.task-list{
    display: flex;
    flex-direction: column;
    grid-gap: 1em;
    gap: 1em;
    margin-top: 1em;
    overflow-y: auto;
    height: 500px;
    padding-right: 1em;
    padding-left: 3px;
}
.task-list::-webkit-scrollbar{
    background-color: #efefef;
}
.task-list::-webkit-scrollbar-thumb{
    background: #095B83;
    border-radius: 4em;
   
}

@media screen and (max-width:390px) {
    .task-list{
        width: 100%;
    }
}
.container{
    display: flex;
    flex-direction: column;
    grid-gap: 1em;
    gap: 1em;
}
.control-buttons{
    display: flex;
    grid-gap: 1em;
    gap: 1em;
    flex-wrap: wrap;
    margin-top: 1em;
}
.titled-card {
    width: 258.99px;
    height: 128px;
    border-radius: 12px;
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    color: white;

}

.titled-card>* {
    margin: 0em;
}

.titled-card p {
    margin-left: 0.2em;
    font-size: 4em;
}

.titled-card h2 {
    margin-left: 0.5em;

}

.titled-card .icon {
    -webkit-transform: rotate(-20.19deg);
            transform: rotate(-20.19deg);
    position: absolute;
    top: 10%;
    right: 0;
    opacity: 0.6;
}


@media screen and (max-width:390px) {
    .titled-card {
        width: auto;
        height: 82px;
        font-size: 0.8em;
    }

    .titled-card .icon {
        font-size: 6em;
    }
}
.statisics-list{
    display: flex;
    flex-direction: column;
    grid-gap: 1em;
    gap: 1em;
    margin-top: 1em;
    flex-wrap: wrap;
}

@media screen and (max-width:390px) {
    .statisics-list{
        width: 100%;
        /* flex-direction: row; */
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
}
.search-field{
    width: 100%;
    height: 4em;
    display: flex;
    align-items: center;
    justify-content: flex-start;

    border-radius: 12px;
    margin-top: 1em;
}
.search-field input{
    width: 80%;
    border: none;
    outline: none;
    background-color: transparent;
    font-size: 1em;
}
.search-field > * {
    margin-left: 1em;
}
.box{
    width: 100%;
    display: flex;
    margin: 0 auto;
    grid-gap: 1em;
    gap: 1em;
    justify-content:center ;

}
.box .body{
    width: 80%;
}
@media screen and (max-width:390px) {
    .box{
        width: 90%;
        flex-direction: column;
    }
    .box .body{
        width: 100%;
    }
}
